<template>
<div id="single-blog">
    <h2>{{blog.title}}</h2>
    <article>{{blog.content}}</article>
    <p>作者：{{blog.author}}</p>
    <p>分类：</p>
    <ul>
        <li v-for="categorie in blog.categories">
{{categorie}}
        </li>
    </ul>

</div>
  
</template>

<script>
export default {
   name:'single-blog',
   data(){
      return{
          id:this.$route.params.id,
          blog:{}
      }
   },
   created(){
       this.$http.get('https://blog-43483.firebaseio.com/posts/' + this.id+'.json')
       .then(function(data){
        console.log(data.json())
        return data.json();
        
       })
       .then(function(data){
           console.log(data)
        this.blog=data;
        // console.log(this.blog)
       })
   }
}
</script>


<style scoped>
#single-blog{max-width:960px;margin:0 auto;background:#eee;padding:20px;border:1px dotted #aaa}
</style>





