<template>
   <nav>
      <ul>
          <li><router-link to="/" exact>博客</router-link><router-link to="/add" exact>写博客</router-link></li>
      
      </ul>  
   </nav> 
</template>

<script>
export default {
    name:'blog-header'
}
</script>

<style scoped>
 ul{    
    list-style: none;
    text-align: center;
    margin: 0;
    padding: 0;}
li{display:inline-block;}
a{text-decoration:none;color:#fff;padding:12px;border-radius:8px;margin:0 10px;}
.router-link-active{background:rgba(255,255,255,.8);color:#444 }
nav{background:crimson;padding:40px 0;margin-bottom:40px}
</style>





