<template>
  <div id="add-blog">
      <h2>添加博客</h2>
      <form v-show="!submmited">
          <label>博客标题</label>
          <input type="text" v-model="blog.title" required >
          <label>博客内容</label>
          <textarea v-model="blog.content"></textarea>
      
      <div id="checkboxes">
          <label>Vue.js</label>
          <input type="checkbox" value="Vue.js" v-model="blog.categories">
            <label>Node.js</label>
          <input type="checkbox" value="Node.js" v-model="blog.categories">
            <label>React.js</label>
          <input type="checkbox" value="React.js" v-model="blog.categories">
            <label>Angular.js</label>
          <input type="checkbox" value="Angular.js" v-model="blog.categories">
      </div>
      <label>作者</label>
      <select v-model="blog.author">
          <option v-for="author in authors">
              {{author}}
          </option>
      </select>
      <input v-on:click.prevent="post" value="添加博客" type="button">
      </form>
      <div v-show="submmited"><h3>提交成功！！</h3></div>
<div id="preview">
    <h3>博客总览</h3>
    <p>博客标题：{{blog.title}}</p>
    <p>博客内容：{{blog.content}}</p>
    <ul>
        <li v-for="category in blog.categories">
           {{category}}
        </li>
    </ul>
    <p>作者:{{blog.author}}</p>
</div>

  </div>
</template>

<script>
export default {
  name: 'add-blog',
  data () {
    return {
      blog:{
          title:"",
          content:"",
          categories:[],
          author:""
          },
      authors:[
          "Jim","Henry","Hemiah","Lucy"
      ],
      submmited:false
    }
  },
 methods:{
     post:function(){
         this.$http.post("https://blog-43483.firebaseio.com/posts.json",this.blog).then(function(data){
            console.log(data);
            this.submmited=true;
         });
     }
 }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#add-blog *{
    box-sizing: border-box
}
#add-blog{
    margin:20px auto;
    max-width: 600px;
    padding:20px
}
label{display:block;margin:20px 0 10px}
input[type=text],textarea,select{display:block;width:100%;padding:8px}
#checkboxes label{display:inline-block;margin-top:0px;}
#checkboxes input{margin-right:10px;}
input[type=button]{display:block;padding:14px;background:crimson;border:0;color:#fff;border-radius:4px;margin:20px 0;font-size:18px;cursor:pointer;}
textarea{height:200px}
#preview{
    padding:10px 20px;
    border:1px dotted #ccc;
    margin:30px 0px;
} 
h3{margin-top:10px}

</style>



